import React,{useState,useEffect} from 'react';
import './MainPage.css';
import {useHistory,withRouter} from 'react-router-dom';
import {Typography,Box,Container,AppBar,Grid} from '@material-ui/core';
import {CustomButton} from '../../Reusable/Button/button';
export default function LandingPage(props){
	 const history = useHistory();

	 const handleSignup=()=>{
	 	history.push('/signuppage');
	 }
	return(
		<Container maxWidth="xl" className="container">
		<AppBar position="static" className="header">
		  <h3>Reader's Korner</h3>
		  <small>uniting the world...</small>
		</AppBar>
		<Box className="sec1">
		 <Grid container className="horizontal-list">
		   <Grid item xs={12} sm={4}>1. Explore a wide categories of blogs</Grid>
		   <Grid item xs={12} sm={4}>2. Write your own blogs</Grid>
		   <Grid item xs={12} sm={4}>3. Go Pro to unlock amazing things</Grid>
		 </Grid>
		</Box>
		<Box className="sec2">
		 <CustomButton className="buttons">Login</CustomButton>
		  <span>OR</span>
		 <CustomButton className="buttons" onClick={handleSignup}>sign up</CustomButton>
		</Box>
		<Box className="image-sec">
		  <img src="./MyProfileCard/main.png"/>
		</Box>
		</Container>)
} 