import {Button,withStyles} from "@material-ui/core";

export const CustomButton = withStyles((theme) => ({
  root: {
    color:'white',
    backgroundImage:'linear-gradient(to right,#ff6666,#ff6666)',
    fontSize:'18px',
    padding:'5px',
    '&:focus':{
    	borderColor:'none',
    	outline:'none',
    }
  },
}))(Button);
